@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,400i,600,700,700i|Roboto');

body {
	padding: 0;
	margin: 0;

}

 footer {
	
	padding-top: 0px;
    background: url(data:image/png;base64,) center/100% ;
    background-size: cover;
     z-index: 3;
    object-fit: cover;
    background-repeat: no-repeat;
    height: 100%;


} 
.pt-190{
	padding-top: 195px;
}


@media(max-width: 768px) {
	footer {
		padding-top: 0px;
		height: auto !important;
	}
	.pt-190{
		padding-top: 0px;
	}

}